var _self;
export default {
	data() {
		return {
			user_name: '',
			mobile: '',
			avatar: ''
		};
	},
	onLoad(options) {
		_self = this;
	},
	onShow() {
		_self.$common.getUserInfo(userinfo=>{
			_self.user_name = userinfo.nickname;
			_self.mobile = userinfo.mobile;
			_self.avatar = userinfo.avatar;
		})
	},
	onBackPress() {},
	methods: {
		onChange(type, msg) {
			_self.$Router.push({
				name: 'change',
				params: {
					page_type: type,
					msg: msg,
					user_name:type==1?_self.user_name:""
				}
			});
		},
		uploadAvatar(rsp) {
			let {path} = rsp
			_self.avatar = path;
			_self.$ajax({
				path: _self.$api.profile,
				data: {
					avatar:_self.avatar
				},
				success(res) {
					let { data, code, msg } = res.data;
					uni.showToast({
						title: msg,
						icon: 'success',
						success() {
							setTimeout(()=>{
								uni.hideToast()
							},600)
						}
					});
				}
			});
		},
		changeAvatar(){
			_self.$refs.avatar.fChooseImg('1',{
				selWidth: "600upx", selHeight: "600upx",
				expWidth: '600upx', expHeight: '600upx'
			});
		},
		doLogout() {
			_self.$ajax({
				path: _self.$api.logout,
				data: {},
				success(res){
					let { data, code, msg } = res.data;
					uni.showToast({
						title:msg,
						icon: 'success',
						success() {
							uni.removeStorage({
								key: 'USERINFO',
								success: function(res) {
									setTimeout(() => {
										uni.hideToast()
										_self.$Router.replaceAll({ name: 'login', params: {} });
									}, 1000);
								}
							});
						}
					});
				}
			})
		}
	}
};
